.entry{
    border: 10px;
    border-style: solid;
    border-color: black;
    font-size: 17px;
    font-stretch: 100%;
    margin: 10px;
    padding: 10px;
    max-width: 300px;
    border-radius: 15px;
    flex: auto;
    background-color: aquamarine;
    background-image: linear-gradient( rgb(192, 255, 234), rgb(72, 194, 255));
    color: black;
    overflow: auto;
}

@media screen and (min-width : 700px) {
    .gridder{
        display: grid;
        grid-template-columns: repeat(4, minmax(200px,1fr));
        grid-auto-rows: auto;
    }
}

@media screen and (max-width : 700px) {
    .gridder{
        display: grid;
        grid-template-columns: 1;
        grid-auto-rows: auto;
    }
}

.weather-selection{
    border-color: black;
    border-width: 5px;
    border-style: outset;
    width: 100px;
}


.Weather{
    background-color: aqua;
}
.Weather-header{
    padding: 10px;
    background-color: rgb(0, 27, 177);
    background-image: linear-gradient(rgb(0,27,177), rgb(0, 162, 255));
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    overflow: auto;
}